import React from 'react';
import {Row, Col, Card,Menu,Dropdown,Button,Icon} from 'antd';
import OrderItemList from './OrderItemList';
import styles from './Order.css';

function OrderStep2({order, currentChannel, onPayBtn, qrUrl}) {
  const menu = (
    <Menu onClick={onPayBtn}>
      <Menu.Item key="0"></Menu.Item>
      <Menu.Item key="1"></Menu.Item>
    </Menu>
  );
  return (order && order.data?
    <div className={styles.normal}>
      <OrderItemList {...order.data} readonly="true"/>
      <Row>
        <Col lg={{span:12, offset:12}} >
          <Card className="userInfo" title="">
            <Row>
              <Col span={5}>:</Col>
              <Col>{order.data.mobile}</Col>
            </Row>
            <Row>
              <Col span={5}>:</Col>
              <Col>{order.data.orderName}</Col>
            </Row>
            <Row>
              <Col span={5}>:</Col>
              <Col>{order.data.address}</Col>
            </Row>
          </Card>
        </Col>
      </Row>
        <Row>
          <Col lg={{span:5,offset:14}} md={12} xs={12}>
          </Col>
          <Col lg={5} md={12} xs={12} style={{textAlign:'right'}}>
            <Button type="primary" style={{width: '100%'}} onClick={onPayBtn}></Button>
          </Col>
      </Row>
    </div>
      :<div></div>
  );
}



export default OrderStep2;



